  font-size: 10px;
  line-height: 12px;
  color: #A9A9A9;
}
@media screen and (max-width: 480px) {
  .footer-policy,
  .footer-copyright {
    width: calc(100% - 16px);
  }
}
@media only screen and (max-width: 479.98px) {
  .footer-policy,
  .footer-copyright {
    text-align: center;
  }
}

@media only screen and (min-width: 479.98px) {
  .footer-policy__wrap {
    max-width: 310px;
  }
}
.footer-policy a {
  color: #A9A9A9;
}

