/*--------------------header----------------------*/
.header__top-inner {
  position: relative;
  height: 68px;
  padding-top: 13px;
  padding-bottom: 25px;
  display: flex;
  align-items: flex-end;
  font-size: 12px;
  line-height: 15px;
}

.icon-raskhodka {
  color: #E63600;
}

.header__logo {
  max-width: 134px;
  flex: 0 0 134px;
  margin-right: 24px;
  position: relative;
  z-index: 3;
}
.header__logo img {
  display: block;
  max-width: 100%;
}

.loc-header {
  margin-right: 20px;
}

.loc-header__selected {
  padding: 0;
  background-color: transparent;
  border: none;
  font-size: 12px;
  line-height: 15px;
  color: rgba(0, 0, 0, 0.6);
  font-family: "Inter", sans-serif;
  cursor: pointer;
}
.loc-header__selected .icon-loc {
  color: rgba(0, 61, 158, 0.6);
  font-size: 20px;
}
@media only screen and (min-width: 947.98px) {
  .loc-header__selected .icon-loc {
    display: none;
  }
}
.loc-header__selected .icon-chev-down {
  color: #A6A6A6;
  font-size: 5px;
  vertical-align: middle;
}

.loc-header__panel {
  position: absolute;
  left: 0;
  top: calc(100% - 14px);
  min-width: 304px;
  padding: 16px 24px 15px 24px;
  visibility: hidden;
  opacity: 0;
  transition: all 0.4s;
  display: flex;
  align-items: center;
  background: #FFFFFF;
  border: 1px solid #E4E4E4;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
  z-index: 2;
}
.loc-header.opened .loc-header__panel {
  visibility: visible;
  opacity: 1;
}

.loc-header__city {
  display: flex;
  align-items: center;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  color: rgba(196, 196, 196, 0.8);
}

.loc-header__city-value {
  margin-left: 10px;
  color: rgba(0, 0, 0, 0.8);
}

.loc-header__city-buttons {
  margin-left: 28px;
  display: flex;
  align-items: center;
}

.loc-header__yes,
.loc-header__no {
  cursor: pointer;
  text-transform: uppercase;
  padding: 0;
  background-color: transparent;
  border: none;
}

.loc-header__no {
  margin-right: 24px;
  color: #717171;
}

.loc-header__yes {
  font-weight: 700;
}

.header__phone {
  color: rgba(0, 0, 0, 0.6);
  text-decoration: none;
}

@media only screen and (max-width: 947.98px) {
  .phone-header__val {
    display: none;
  }
}
.phone-header .icon {
  color: rgba(0, 61, 158, 0.6);
  font-size: 19.87px;
}
@media only screen and (min-width: 947.98px) {
  .phone-header .icon {
    display: none;
  }
}

.menu-header {
  margin-left: auto;
}
.menu-header .toggle-mnu {
  display: none;
}

.menu-header__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.menu-header__item {
  list-style-type: none;
}
.menu-header__item:not(:last-child) {
  margin-right: 16px;
}
.menu-header__item .single-dropdown {
  width: 204px;
  padding: 20px;
  left: auto;
  right: 0;
  background-color: #fff;
  border: 1px solid #E4E4E4;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
  z-index: 2;
}
.menu-header__item--for-buyer .single-dropdown {
  right: auto;
  left: 0;
}

.menu-header__link {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.6);
  font-weight: 700;
}

.menu-header__arrow {
  position: relative;
  display: inline-block;
  margin-left: 4px;
  vertical-align: middle;
  padding: 0;
  background-color: transparent;
  border: none;
}
.menu-header__arrow .icon {
  vertical-align: middle;
  font-size: 5px;
  color: #A6A6A6;
}

.single-dropdown__item:not(:last-child) {
  margin-bottom: 16px;
}

.single-dropdown__link {
  color: rgba(0, 0, 0, 0.8);
  text-decoration: none;
}
@media (any-hover: hover) {
  .single-dropdown__link:hover {
    text-decoration: underline;
  }
}

.header__bottom {
  padding-bottom: 16px;
  transition: all 0.3s;
  background-color: #fff;
}
._scroll .header__bottom {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  z-index: 10;
}

.header__logo-fixed {
  display: block;
  opacity: 0;
  width: 0;
  height: 0;
  visibility: hidden;
  transition: all 0.3s;
}
.header__logo-fixed img {
  display: block;
  max-width: 100%;
}
._scroll .header__logo-fixed {
  margin-right: 40px;
  width: 40px;
  height: 40px;
  opacity: 1;
  visibility: visible;
}

.header__catalog-toggle {
  padding: 0;
  background-color: transparent;
  border: none;
  display: flex;
  align-items: center;
  padding: 5px 13px;
  flex: 0 0 144px;
  height: 40px;
  max-width: 144px;
  color: #fff;
  background-color: #003D9E;
}
.header__catalog-toggle .toggle-mnu {
  width: 17px;
  height: 13px;
  margin-right: 10px;
  flex: 0 0 auto;
  position: static;
  margin-top: 0;
}
.header__catalog-toggle .toggle-mnu span, .header__catalog-toggle .toggle-mnu span:after, .header__catalog-toggle .toggle-mnu span:before {
  height: 2.5px;
  background-color: #fff;
  border-radius: 3px;
}
.header__catalog-toggle .toggle-mnu span:before {
  top: 6px;
}
.header__catalog-toggle .toggle-mnu span:after {
  top: 12px;
}

.catalog-toggle-header {
  margin-right: 16px;
}

.catalog-toggle__name {
  font-size: 16px;
  line-height: 10px;
  font-family: "NavigoMedium", sans-serif;
  text-transform: uppercase;
  transform: translateY(2px);
}

.search-header {
  flex: 1 1 auto;
}

.search-header__form {
  position: relative;
}

.search-header__input {
  border-radius: 4px;
}

.search-header__send {
  position: absolute;
  right: 0;
  top: 0;
}

.header__actions {
  margin-left: auto;
}

.header__account {
  margin-left: 25px;
}

.header__tools {
  display: block;
}

.header__favourites {
  display: block;
}

.header__compare {
  display: block;
}

.header__city-list {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  padding-bottom: 48px;
  background-color: #fff;
  visibility: hidden;
  opacity: 0;
  transition: all 0.4s;
}
.cities-opened .header__city-list {
  opacity: 1;
  visibility: visible;
}
.header__city-list .toggle-mnu {
  margin-top: 0;
  top: 22px;
  right: 18px;
}

.location-search__wrapper {
  padding-top: 68px;
}

.location-search__header {
  display: flex;
  align-items: center;
  margin-bottom: 43px;
  color: #000;
}

.location-search__key {
  flex: 0 0 150px;
  margin-right: 5px;
  font-weight: 700;
  font-size: 22px;
  line-height: 20px;
}

.location-search__list {
  column-count: 3;
}

.location-search__item {
  list-style-type: none;
}
.location-search__item:not(:last-child) {
  margin-bottom: 11px;
}
.location-search__item.active {
  font-weight: 700;
}

.location-search__link {
  font-size: 14px;
  line-height: 17px;
  text-decoration: none;
  color: #000;
